.item {
    flex: 0 0 25%;
    cursor: pointer;
}

#root {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.main {
    flex: 1 1 auto;
}

@media (max-width: 992px) {
    .item {
        flex: 0 0 50%;
    }
}

@media (max-width: 400px) {
    .item {
        flex: 0 0 100%;
    }
}

.item-container {
    position: relative;
    padding: 60%;
}

.basket {
    display: block;
    width: 32px;
    height: 32px;
}

.item__img {
    object-fit: contain;
    position: absolute;
    width: 80%;
    height: 80%;
    top: 10%;
    left: 8%;
}

.footer {
    text-align: center;
    background-color: #e7f3ff;
    font-size: 16px;
    width: 100%;
    padding: 10px;
    line-height: 16px;
}

.basket-title {
    font-size: 30px;
    font-weight: 700;
    text-align: center;
    margin-top: 50px;
}

.basket-row {
    display: flex;
    padding: 10px 0;
    background-color: #e7f3ff;
    margin-top: 20px;
}

.basket-row span {
    flex: 20%;
    text-align: center;
    letter-spacing: 2px;
}

.basket-row-footer {
    display: flex;
    padding: 10px 0;
    background-color: #e7f3ff;
}

.basket-row-footer span {
    flex: 20%;
    text-align: center;
}

.basket-item-row {
    display: flex;
    padding: 25px 0;
    align-items: center;
}

.basket-item {
    flex: 20%;
    text-align: center;
}
.item-image-container {
    position: relative;
    padding: 5%;
}

.basket-item-img {
    position: absolute;
    height: 100%;
    width: 50%;
    top: 0;
    left: 30%;
    object-fit: contain;
}

.basket-item-cancel {position: relative; cursor: pointer; padding: 10px}
.basket-item-cancel:before, .basket-item-cancel:after {content: ""; position: absolute; width: 24px; height: 4px; background: black;}
.basket-item-cancel:before {transform: rotate(45deg);}
.basket-item-cancel:after {transform: rotate(-45deg);}

.arrow-down img, .arrow-up img {
    width: 20px;
    height: 20px;
}

.count {
    display: flex;
    justify-content: center;
    align-items: center;
}

.arrow-up {
    transform: rotate(-90deg) translateY(0%);
    cursor: pointer;
}

.arrow-down {
    transform: rotate(90deg) translateY(-20%);
    cursor: pointer;
}

.basket-item-counter {
    padding: 10px;
    font-size: 18px;
}

.navbar {
    background-color: rgb(127, 9, 70);
}